/*eslint-disable*/
import React from "react";
import { NavLink, useLocation } from "react-router-dom";
// @material-ui/core components
import { makeStyles } from "@material-ui/core/styles";
import Drawer from "@material-ui/core/Drawer";
import List from "@material-ui/core/List";
import Divider from "@material-ui/core/Divider";
import ListItem from "@material-ui/core/ListItem";
import ListItemIcon from "@material-ui/core/ListItemIcon";
import ListItemText from "@material-ui/core/ListItemText";

import styles from "./sidebarStyle"

const useStyles = makeStyles(styles);

export default function Sidebar(props) {
    const classes = useStyles();

    const brand = (
        <div className={classes.logo}>
            <a
                href=""
                className={classes.logoLink}
                target="_blank"
            >
                <div className={classes.logoImage}>
                    <img src={props.logo} alt="logo" className={classes.img} />
                </div>
                {props.logoText}
            </a>
        </div>
    );
    
    const category = props.route;
    const list = (
        <div>
            <Divider />
            <List className={classes.list}>
                {category.map((attributes, index) => {
                    return (
                        <NavLink
                            to={attributes.path}
                            className={classes.item}
                            activeClassName="active"
                            key={index}
                        >
                            <ListItem button>
                                <ListItemIcon><attributes.icon/></ListItemIcon>
                                <ListItemText 
                                    primary={attributes.name}
                                    className={classes.itemText}
                                />
                            </ListItem>
                        </NavLink>
                    );
                })}
            </List>
        </div>
    );

    return (
        <div>
            <Drawer
                className={classes.drawer}
                classes={{
                    paper: classes.drawerPaper,
                }}
                variant={props.isMobile ? "temporary" : "persistent"}
                anchor="left"
                open={props.open}
                onClose={props.handleDrawerToggle}
            >
                {brand}
                <div className={classes.sidebarWrapper}>
                    {list}
                </div>
                {props.image !== undefined ? (
                    <div
                        className={classes.background}
                        style={{ backgroundImage: "url(" + props.image + ")" }}
                    />
                ) : null}
            </Drawer>
        </div>
    );
}
