import React from "react";
import { Route, Switch } from "react-router-dom";

import { makeStyles } from "@material-ui/core/styles";
import Sidebar from "../component/template/sidebar/sidebar";
import Navbar from "../component/template/navbar/navbar";

import logo from "../image/compass_mark.png";
import sidebarBackground from "../image/sidebar_background.jpg";
import routes from "../route";

import styles from "./homeStyle";

const useStyles = makeStyles(styles);

const switchRoutes = (
    <Switch>
        {routes.map((prop, index) => {
            return (
                <Route
                    path={prop.path}
                    component={prop.component}
                    key={index}
                />
            );
        })}
    </Switch>
);

export default function Main() {
    const classes = useStyles();
    const criticalWidth = 960;
    const [sidebarOpen, setSidebarOpen] = React.useState(window.innerWidth >= criticalWidth);
    const [isMobile, setIsMobile] = React.useState(window.innerWidth < criticalWidth);

    React.useEffect(() => {
        window.addEventListener("resize", resizeFunction);
    }, []);

    const resizeFunction = () => {
        setSidebarOpen(window.innerWidth >= criticalWidth);
        setIsMobile(window.innerWidth < criticalWidth);
    };

    const handleDrawerToggle = () => {
        setSidebarOpen(!sidebarOpen);
    };

    return (
        <div>
            <Sidebar
                logo={logo}
                image={sidebarBackground} // background
                handleDrawerToggle={handleDrawerToggle}
                open={sidebarOpen}
                isMobile={isMobile}
                logoText={"compass"}
                route={routes}
            />
            <div>
                <Navbar
                    handleDrawerToggle={handleDrawerToggle}
                    isMobile={isMobile}
                />
                <div className={classes.content}>
                    <div className={classes.container}>{switchRoutes}</div>
                </div>
            </div>
        </div>
    );
}