import Hero from "./component/view/hero";
import Card from "./component/view/card";
import AssignmentInd from "@material-ui/icons/AssignmentInd";
import SimCard from "@material-ui/icons/SimCard";

const routes = [
    {
        path: "/hero",
        name: "Hero",
        icon: AssignmentInd,
        component: Hero,
    },
    {
        path: "/card",
        name: "Card",
        icon: SimCard,
        component: Card,
    },
];

export default routes;